<template>
  <el-form-item
    v-bind="schema"
    :prop="vkey"
    :label="schema.label"
    :rules="schema.rules"
  >
    <component
      v-bind="mergeAttrs"
      v-model="formatValue"
      :is="componentFormat"
      :vkey="vkey"
      :schema="schema"
    ></component>
  </el-form-item>
</template>

<script>
import { T } from "../../utils/index";
export default {
  name: "YoSchemaFormItem",
  props: {
    value: {
      required: true,
    },
    vkey: {
      type: String,
      required: true,
    },
    schema: {
      type: Object,
      required: true,
    },
  },
  computed: {
    // 
    //   this.formatValue = xxx
    formatValue: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
      },
    },
    //  Attrs attrs
    mergeAttrs() {
      if (this.schema.attrs && T(this.schema.attrs) === "object") {
        return {
          ...this.$attrs,
          ...this.schema.attrs,
        };
      }
      return this.$attrs;
    },
    // 
    // 1schema
    // 2schemaschematype 
    // 3schematype  value  
    // 4   input 
    componentFormat() {
      if (this.schema && this.schema.component) {
        return this.schema.component;
      }
      let MODELTYPE = "";
      if (this.schema && this.schema.type) {
        MODELTYPE = this.schema.type;
      } else {
        MODELTYPE = T(this.formatValue);
      }
      switch (MODELTYPE) {
        case "number":
          return "el-input-number";
        case "array":
          return "yo-schema-array";
        case "boolean":
          return "yo-schema-boolean";
        case "object":
          return "yo-schema-object";
      }
      return "el-input";
    },
  },
};
</script>
